<!--
 * @Author: Marlon
 * @Date: 2020-02-18 10:24:04
 * @LastEditors: Marlon
 * @LastEditTime: 2020-02-21 00:45:24
 * @Description: 项目统计
 -->
<template>
  <div class="project-statistics">
    <ul class="project-statistics-edit-area">
      <li>
        <el-button type="primary" size="medium" @click="showDialog({type:'新建'})">新建</el-button>
      </li>
      <li>
        <el-tooltip class="item" effect="dark" content="比较两个或多个项目之间的元数据的差异" placement="top">
          <el-button type="primary" size="medium">比较</el-button>
        </el-tooltip>
      </li>
    </ul>
    <div class="project-statistics-table">
      <TableDefault
        :TTitle="tableTitle"
        :TData="tableData"
        :TIsShowCheckBox="true"
        :TIsShowPage="true"
        :TEdit="TEdit"
        :TClickItem="TClickItem"
        @editBtnCb="editBtnCb"
      ></TableDefault>
    </div>
    <!-- 弹出框 -->
    <FormDialog
      :FDVisible="FDVisible"
      :FDTitle="FDTitle"
      FDWidth="500"
      :FDForm="FDForm"
      @editBtnCb="editBtnCbDiglog"
    ></FormDialog>
  </div>
</template>

<script>
import mixin from "mixin";

export default {
  mixins: [mixin],
  data() {
    return {
      // 编辑-表格配置
      TEdit: {
        show: true,
        title: "操作",
        editTypes: [
          {
            label: "编辑"
          },
          {
            label: "删除",
            css: "edit-button-color-1"
          }
        ]
      },
      tableTitle: [
        {
          prop: "name",
          label: "名称"
        },
        {
          prop: "date",
          label: "数据库个数"
        },
        {
          prop: "address",
          label: "数据表个数"
        }
      ],
      tableData: [
        {
          date: "122",
          name: "通用",
          address: "45"
        },
        {
          date: "122",
          name: "海淀",
          address: "45"
        },
        {
          date: "122",
          name: "山西",
          address: "45"
        },
        {
          date: "122",
          name: "济南",
          address: "45"
        },
        {
          date: "122",
          name: "山东",
          address: "45"
        },
        {
          date: "122",
          name: "贵州",
          address: "45"
        },
        {
          date: "122",
          name: "福州",
          address: "45"
        },
        {
          date: "122",
          name: "泰安",
          address: "45"
        },
        {
          date: "122",
          name: "南京",
          address: "45"
        },
        {
          date: "122",
          name: "广州",
          address: "45"
        }
      ],
      // 是否显示弹出框
      FDVisible: false,
      FDTitle: "",
      // 弹出框配置
      FDForm: [
        {
          type: "input",
          label: {
            name: "名称",
            width: "60"
          },
          width: "120",
          defaultModel: "",
          placeholder: ""
        },
        {
          type: "input",
          label: {
            name: "编码",
            width: "60"
          },
          width: "120",
          defaultModel: "",
          placeholder: ""
        },
        {
          type: "textarea",
          label: {
            name: "备注",
            width: "60"
          },
          width: "400",
          defaultModel: "",
          placeholder: "",
          autosize: {
            min: 2,
            max: 4
          }
        }
      ],
      // 可跳转的值
      TClickItem: [
        {
          link: "/",
          cells: ["address"]
        }
      ]
    };
  },
  methods: {
    // 表格-回调
    editBtnCb(editItem) {
      console.log(editItem);
      let text = editItem.label;
      switch (text) {
        case "删除":
          // TODO 发请求
          this.$message({
            type: "success",
            message: "删除成功!"
          });
          return;
        case "编辑":
          this.showDialog({ type: text });
          return;
        default:
          break;
      }
    },
    // 弹出框-回调
    editBtnCbDiglog(obj, formReq) {
      this.FDVisible = false;
      if (obj === "确定") {
        // TODO 发请求
        console.log(formReq);
      }
    },
    // 显示弹出框并修改配置
    showDialog({ type }) {
      switch (type) {
        case "新建":
          this.FDTitle = "新建项目";
          this.FDVisible = true;
          //   this.FDForm=[]
          return;
        case "编辑":
          this.FDTitle = "编辑项目信息";
          this.FDVisible = true;
          //   this.FDForm=[]
          return;
        default:
          return;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.project-statistics {
  &-edit-area {
    overflow: hidden;
    li {
      float: left;
      margin: 2px 15px;
    }
  }
  &-table {
    margin: 20px 0;
  }
}
</style>



